<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>Author:{{blog.author}}</p>
        <p>Category:</p>
        <ul>
            <li v-for="category in blog.categories">{{category}}</li>
        </ul>
        <button @click="deleteSingleBlog">Delete</button>
        <router-link :to="'/edit/'+id" >Edit</router-link>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name:"single-blog",
    data(){
        return{
            id:this.$route.params.id,//博客路由参数
            blog:{}
        }
    },
    created(){
        // this.$http.get('https://myblogs-123.firebaseio.com/posts/' + this.id +'.json')
        axios.get('/posts/' + this.id +'.json')
        .then((data)=>{
            // console.log(data);
            this.blog = data.data;//使用axios不再是data.json();
            // this.blog = data.body;
        })
    },
    methods:{
        deleteSingleBlog(){
            this.$http.delete("/posts/" + this.id +'.json')
            .then(redponse =>{
                this.$router.push({path:'/'});
            })
        }
    }
}
</script>
<style >
    #single-blog{
        max-width: 960px;
        margin:0 auto;
        padding: 20px;
        background: #eee;
        border: 1px dotted #aaa;
    }    
</style>


