// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// import VueResource from 'vue-resource'//引入后，可以使用get，post
import VueRouter from 'vue-router'//引入路由模块
import App from './App'
import Routes from './routes'
import axios from 'axios'//引入后，可以使用get，post(推荐)

//全局配置,局部url前自动添加上
axios.defaults.baseURL = 'https://myblogs-123.firebaseio.com'
// axios.defaults.header.common['Authorization'] = Token    
//拓展axios，如果拓展太多，可以新建一个js文件，在这里引入
// axios.defaults.header.post['Content-type']='application/urlencode'
// axios.defaults.header.get['Accepts']='application/json'

Vue.config.productionTip = false
// Vue.use(VueResource)//使用中间件
Vue.use(VueRouter)//使用路由

//在main.js定义指令，是全局指令。不太建议

Vue.directive('theme',{//自定义指令
  bind(el,binding,vnode){
    if(binding.value == 'wide'){//binging.value绑定的值。对应后面的
      el.style.maxWidth = '1260px';
    }else if(binding.value == 'narrow'){
      el.style.maxWidth= "600px";
    }  
    if(binding.arg == 'column'){//binging.arg绑定的参数 ，对应前面的。
      el.style.background = '#6677cc';
      el.style.padding = "20px";
    }
  }
})

// Vue.directive('rainbow',{       //全局指令
//   bind(el,binding,vnode){
//     el.style.color = "#" +Math.random().toString(16).slice(2,8);
//   }
// })

// Vue.filter("to-uppercase",function(value){
//   return value.toUpperCase();
// })            //在ShowBlogs.vue文件里面定义了，作为局部过滤器，此处留档

Vue.filter("snippet",function(value){
  return value.slice(0,100) + "..."; 
})
const router = new VueRouter({
  routes:Routes,
  mode:'history'
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router:router//或者只写router？
})



